import React, { Component } from 'react';

import { Route } from 'react-router-dom';
import Content from '../../Components/Content/Content';
import Navbar from '../../Components/Navbar/Navbar';
import About from '../../Components/About/About';
import Footer from '../../Components/Footer/Footer';
// import Contact from '../../Components/Contact/Contact';
import Contact from '../../Containers/Contact/Contact';

// fontAwesome library
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { faFacebook, faInstagram, faTwitter, faYoutube} from '@fortawesome/free-brands-svg-icons';
import { faHeart, faCoffee } from '@fortawesome/free-solid-svg-icons';

library.add(
  fab, faFacebook, faInstagram, faTwitter, faYoutube,
  faHeart, faCoffee
  )

class Index extends Component {
  render() {
    return(
     <div>
      <Navbar/>
      <Route path="/carloshmayo/" exact component={Content}/>
      <Route path="/carloshmayo/about" exact component={About}/>
      <Route path="/carloshmayo/contact" exact component={Contact}/>
      <Footer/>
     </div>
   
    );
  }
}

export default Index;