import React, { Component } from 'react';

import Form from '../../Components/Contact/Contact';

class Contact extends Component {
  state = {
    name: '',
    lastname: '', 
    email: '',
    message: ''
  }

  // This is the functio for the submit button
  onSubmitHandle = (event) => {
    event.preventDefault();
    console.log("I have clicked the submit button")
    
    alert("My names are: " + this.state.name + ", my lastnames are: " + this.state.lastname + " and I am " + this.state.age);
  }

  // Function for the name
  onNameChangeHandle = (event) => {
    this.setState({name: event.target.value})
    console.log(this.state.name);
  }

  // Function for the lastname
  onLastnameChange = (event) => {
    this.setState({lastname: event.target.value});
    console.log(this.state.lastname);
  }
  
  onAgeChange = (event) => {
    this.setState({age: event.target.value});
    console.log(this.state.age);
  }

  render() {
    return(
      <Form 
        action={this.onSubmitHandle} 
        value={this.state.name} 
        changeName={this.onNameChangeHandle}
        lastnameValue={this.state.lastname}
        changeLastname={this.onLastnameChange}
        ageValue={this.state.age}
        changeAge={this.onAgeChange}/>
        
    );
  }
};

export default Contact;